import React from 'react';
import './category-style.scss';
import { Link } from 'react-router-dom';


function CategoryBoxStyle1(props) {
  return (
    <div className={`category-item category-style1 ${props.className || ''}`}>
      <div className="category-icon">
        <i className={props.icon}></i>
        <h5 className="category-title">{props.title}</h5>
      </div>
      <div className="category-list">
        {props.list && (
          <ul>
            {props.list.map((list, index) => (
              <li key={index}>{list.item}</li>
            ))}
          </ul>
        )}
        {props.content && <p className="text-center mb-0">{props.content}</p>}
      </div>
    </div>
  );
}

function CategoryBoxStyle2(props) {
  return (
    <div className={`category-item category-style2 ${props.className || ''}`}>
    <Link
          className="icon-btn"
          to={{ pathname: `/service-details/${props.id}` }}
        >
      <div className="category-icon">
        <i className={props.icon}></i>
        <h5 className="category-title">{props.title}</h5>
        

      </div>
      <div className="category-list">
        <ul>
          {props.list.map((list, index) => (
            <li key={index}>{list.item}</li>
          ))}
        </ul>
      </div>
      </Link>

    </div>
  );
}

function CategoryBoxStyle3(props) {
  return (
    <div className={`category-item category-style03 ${props.className || ''}`}>
      <div className="category-list">
        <h4 className="category-title">{props.title}</h4>
        <ul>
          {props.list.map((list, index) => (
            <li key={index}>{list.item}</li>
          ))}
        </ul>
      </div>
      <div className="category-icon">
        <img className="img-fluid w-75" src={props.image} />
      </div>
    </div>
  );
}

export default CategoryBoxStyle1;
export { CategoryBoxStyle2, CategoryBoxStyle3 };
